import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IonicSlides } from '@ionic/angular';
import { NgxSpinnerService } from 'ngx-spinner';
import { ICategory, IRecoAdvice } from 'src/shared/models/type';
import { StorageService } from 'src/shared/services/storage.service';
import SwiperCore, { Pagination } from 'swiper';
import { LocalStorageConstants } from '../app.constants';

SwiperCore.use([Pagination, IonicSlides]);

@Component({
  selector: 'app-recommendation-advice',
  templateUrl: './recommendation-advice.component.html',
  styleUrls: ['./recommendation-advice.component.scss'],
})
export class RecommendationAdviceComponent implements OnInit {
  recoAdvices: IRecoAdvice[];
  categoriesAsMap: Map<string, ICategory>;
  itemsRequired: number = 1;
  constructor(private route: ActivatedRoute,
    private storageService: StorageService,
    private spinner: NgxSpinnerService) {
    this.recoAdvices = JSON.parse(this.route.snapshot.paramMap.get('advices'));
    this.categoriesAsMap = this.storageService.getJSONItemFromLocalStorage(LocalStorageConstants.CATEGORIES_AS_MAP);
  }

  ngOnInit() {
  }

  ionViewDidEnter() {
    this.spinner.hide('appSpinner');
  }

}
